/* Custom font */
body {
    font-family: 'IBM Plex Serif', serif;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'IBM Plex Sans', sans-serif;
}

/* Custom styles for definition lists */

dl dt {
    text-decoration: underline;
}

dl dd { 
    margin: 1rem 0 1rem 2rem;
}

/* Custom heading styles */
h1 {
    font-size: 2.2rem;
    text-transform: uppercase;
}
